@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap; }

body {
  font-weight: 400;
  font-size: 14px;
  color: #111111;
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
::after,
::before {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

a {
  text-decoration: none; }

p,
ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-header__background {
  background-color: #FBFBFB; }

.page-header__section {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 18px 0 18px 0; }

.page-header__wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center; }

.page-header__logo {
  display: block;
  margin-right: 60px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__logo:hover {
  opacity: 0.6;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: -5px; }

.page-header__item {
  margin-right: 25px; }

.page-header__item a {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  color: #707070;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__item a:hover {
  cursor: pointer;
  color: #00FFD8;
  text-decoration: underline;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__contacts {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.page-header__tel {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  color: #222222;
  margin-top: 13px;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__tel:hover {
  color: #00FFD8;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__Feedback {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  color: #000000;
  width: 190px;
  min-height: 46px;
  background-color: #00FFD8;
  display: block;
  text-align: center;
  padding: 11px;
  margin-left: 25px;
  border: 2px solid #00FFD8;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

.page-header__Feedback:hover {
  cursor: pointer;
  border: 2px solid #00FFD8;
  background-color: transparent;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease; }

@media (max-width: 1199px) {
  .page-header__Feedback {
    display: none; }
  .page-header__logo {
    margin-right: 38px; }
  .page-header__logo img {
    width: 86px;
    height: 40px; }
  .page-header__list {
    display: none; }
  .page-header__section {
    padding: 6px 0 2px 0; }
  .page-header__tel {
    margin-top: 0;
    font-size: 14px; }
  .page-header__burger {
    width: 28px;
    height: 22px;
    cursor: pointer;
    position: relative; }
  .page-header__line {
    display: block;
    width: 100%;
    height: 2px;
    background-color: #AFAFAF;
    position: absolute;
    left: 0;
    -webkit-transition: .3s all linear;
    transition: .3s all linear; }
  .page-header__burger:hover .page-header__line {
    background-color: #00FFD8; }
  .page-header__line-first {
    top: 0; }
  .page-header__line-second,
  .page-header__line-third {
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
  .page-header__line-fourth {
    bottom: 0; }
  .page-header__burger-active .page-header__line-first,
  .page-header__burger-active .page-header__line-fourth {
    opacity: 0; }
  .page-header__burger-active .page-header__line-second {
    -webkit-transform: translateY(-50%) rotate(45deg);
            transform: translateY(-50%) rotate(45deg); }
  .page-header__burger-active .page-header__line-third {
    -webkit-transform: translateY(-50%) rotate(-45deg);
            transform: translateY(-50%) rotate(-45deg); }
  .page-header__nav-active {
    display: block;
    position: absolute;
    top: 57px;
    left: 0px;
    text-align: center;
    min-width: 200px;
    background-color: #e7e7e7; }
  .page-header__item {
    padding: 5px 0;
    border-bottom: 1px solid black;
    margin-right: 0; } }

@media (max-width: 767px) {
  .page-header__tel {
    display: none; }
  .page-header__burger {
    width: 18px;
    height: 14px;
    top: -3px;
    right: 0px;
    margin-left: auto; }
  .page-header__section {
    padding-bottom: 0; }
  .page-header__nav-active {
    top: 55px;
    width: 100%; }
  .page-header__wrap {
    width: 100%; } }

.container {
  max-width: 1170px;
  margin: 0 auto; }

@media (max-width: 1199px) {
  .container {
    padding-left: 64px;
    padding-right: 64px;
    margin: 0 auto; } }

@media (max-width: 767px) {
  .container {
    padding: 6px 20px 0; } }
